body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  font-size: 16px; /* Bazowa wielkość fontu dla responsywności */
}

header {
  background-color: #333;
  color: white;
  padding: 1.25rem;
  text-align: center;
}

main {
  padding: 1.25rem;
}

nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 0.625rem;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
}

.sidebar-handle {
  width: 20px;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  background-color: rgb(58, 58, 58);
  transition: background-color 0.3s;
}

.sidebar {
  height: 100%;
  width: 250px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: -250px;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidebar:hover {
  left: 0;
}

.sidebar-handle:hover {
  background-color: rgba(0,0,0,0.1); /* delikatnie widoczny przy hover */
}

.sidebar-handle:hover + .sidebar {
  left: 0;
}

header, main, footer {
  margin-left: 20px; /* Tylko te elementy są przesuwane */
}

.App {
  background-image: url('./bg.jpg');
  background-size: auto;
  min-height: 100vh;
}

/* Media queries dla różnych rozdzielczości */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  nav ul li {
    margin: 0.5rem;
  }

  header, footer {
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  nav ul {
    flex-direction: column;
  }

  nav ul li {
    margin: 0.5rem 0;
  }
}

/* Stylowanie dla tabletów (od 640px) */
@media (min-width: 640px) {
  nav ul li {
    margin: 10px 20px;
  }
}

/* Stylowanie dla desktopów (od 1024px) */
@media (min-width: 1024px) {
  body {
    font-size: 18px;
  }
}